.card {
    background: #fff;
    border-radius: 2px;
    margin-left:0px;
    margin-right:0px;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }

  .card > .img-container {
    height: 100%;
    width:95%;
    overflow: hidden;
    text-align: center;
    
  }
  
  .card > .img-container > img {
    width: 95%
  }